@import "normalize.css";
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("RobotoRegular"), url("fonts/roboto-v30-latin_cyrillic-regular.woff2") format("woff2"), url("fonts/roboto-v30-latin_cyrillic-regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("RobotoBold"), url("fonts/roboto-v30-latin_cyrillic-700.woff2") format("woff2"), url("fonts/roboto-v30-latin_cyrillic-700.woff") format("woff");
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}

img,
svg {
  max-width: 100%;
}

body {
  min-width: 320px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  background-color: white;
}

.container {
  max-width: 1188px;
  padding: 0 39px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.btn {
  padding: 0;
  cursor: pointer;
  background-color: #3F2AFF;
  color: white;
  border: none;
}

.stop-scroll {
  overflow: hidden;
}

.header {
  padding-top: 29px;
  padding-bottom: 24px;
  background-color: #3F2AFF;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.header__svg {
  width: 94px;
  height: 21px;
}
.header__nav {
  max-width: 850px;
}

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
}
.nav__close-btn {
  width: 30px;
  height: 30px;
  display: none;
}
.nav__item:not(:last-child) {
  margin-right: 57px;
}
.nav__link {
  font-size: 18px;
  line-height: 21px;
  font-weight: 400;
  color: white;
}

.burger {
  display: none;
}

.hero {
  background-color: #000;
  background-image: url("img/hero-background.jpg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}
.hero__title {
  margin: 0;
  margin-bottom: 27px;
  font-weight: 700;
  font-size: 60px;
  line-height: 60px;
  color: white;
}
.hero__col {
  display: flex;
  justify-content: center;
}
.hero__preview {
  display: block;
  padding-top: 15px;
  align-self: center;
}
.hero__content {
  padding: 130px 0 130px 0;
}
.hero__desc {
  margin: 0;
  margin-bottom: 63px;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  color: white;
}
.hero__btn {
  font-size: 18px;
  line-height: 21px;
  font-weight: 700;
  border-radius: 20px;
  padding: 24px 75px;
}

.benefits {
  padding: 80px 0 80px 0;
}
.benefits__content {
  margin-bottom: 23px;
  padding-left: 190px;
  background-image: url("img/benefits.png");
  background-repeat: no-repeat;
  background-position: center left;
  background-size: 160px 142px;
}
.benefits__title {
  max-width: 380px;
  margin: 0;
  margin-bottom: 17px;
  font-size: 40px;
  line-height: 40px;
  font-weight: 700;
  color: #2E2B44;
}
.benefits__desc {
  max-width: 585px;
  margin: 0;
  font-weight: 400;
  color: #666;
}
.benefits__list {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style-type: none;
}
.benefits__card {
  display: flex;
  align-items: center;
}
.benefits__svg {
  min-width: 80px;
  min-height: 80px;
  margin-right: 29px;
}
.benefits__paragraph {
  margin: 0;
  font-weight: 700;
  line-height: 19px;
  color: #666;
}

.instructors {
  padding: 75px 0;
  background-color: #212133;
}
.instructors__title {
  margin: 0;
  margin-bottom: 23px;
  font-weight: 700;
  color: white;
  font-size: 40px;
  line-height: 40px;
}
.instructors__list {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style-type: none;
}
.instructors__img {
  width: 100%;
  border-top-right-radius: 23px;
  border-top-left-radius: 23px;
  overflow: hidden;
}
.instructors__preview {
  width: 100%;
  display: block;
  margin-bottom: 23px;
}
.instructors__name {
  margin: 0;
  margin-bottom: 7px;
  color: white;
  font-size: 18px;
  line-height: 27px;
  font-weight: 700;
}
.instructors__desc {
  margin: 0;
  color: #85859B;
  font-weight: 400;
}

.courses {
  padding: 75px 0 81px 0;
}
.courses__row {
  list-style-type: none;
  padding: 0;
  margin-top: 0;
  margin-bottom: 0;
}
.courses__row:first-of-type {
  margin-bottom: 29px;
}
.courses__col {
  display: flex;
}
.courses__card {
  position: relative;
  background-color: #EDF0F9;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  border-radius: 20px;
}
.courses__title {
  margin: 0;
  margin-bottom: 23px;
  font-weight: 700;
  color: #212133;
  font-size: 40px;
  line-height: 40px;
}

.course__time {
  position: absolute;
  top: 11px;
  right: 5px;
  font-weight: 400;
  line-height: 19px;
  color: white;
}
.course__img {
  object-fit: contain;
  width: 100%;
}
.course__preview {
  display: flex;
  width: 100%;
  margin-bottom: 31px;
}
.course__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  flex-grow: 1;
  padding: 0 30px 30px 30px;
}
.course__info {
  margin-bottom: 50px;
}
.course__title {
  margin: 0;
  margin-bottom: 7px;
  font-size: 18px;
  line-height: 27px;
  font-weight: 700;
  color: #212133;
}
.course__desc {
  margin: 0;
  font-weight: 400;
  color: #666;
}
.course__btn {
  padding: 20px 59px;
  font-weight: 700;
  line-height: 18.75px;
  border-radius: 20px;
}
.course__svg {
  width: 24px;
  height: 24px;
  vertical-align: middle;
}

.footer__socials {
  margin: 0;
  margin-bottom: 4px;
  padding: 30px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: #2E2B44;
  list-style-type: none;
}
.footer__item:not(:last-child) {
  margin-right: 34px;
}
.footer__icon {
  display: flex;
}
.footer__link {
  color: #3F2AFF;
  font-size: 13px;
  font-weight: 400;
  line-height: 15px;
}
.footer__text {
  display: block;
  text-align: center;
  font-size: 13px;
  font-weight: 400;
  line-height: 15px;
}

.reg {
  font-size: 100%;
  bottom: 0;
  top: 0;
}

@media (max-width: 1199.98px) {
  .instructors {
    padding: 75px 0 50px 0;
    background-color: #2E2B44;
  }
  .instructors__item:nth-child(-n+2) {
    margin-bottom: 25px;
  }
  .instructors__img {
    border-top-right-radius: 35px;
    border-top-left-radius: 35px;
  }
}
@media (max-width: 991.98px) {
  .hero {
    background-image: url("img/hero-background-768.jpg");
    background-position: center bottom;
  }
  .hero__content {
    padding: 130px 0 132px 0;
  }
  .hero__title {
    margin-bottom: 15px;
    font-size: 50px;
    line-height: 50px;
  }
  .hero__desc {
    margin-bottom: 65px;
  }
  .hero__preview {
    padding-top: 0;
    padding-bottom: 26px;
  }
  .benefits {
    padding: 80px 0 76px 0;
  }
  .benefits__card {
    flex-direction: column;
    align-items: flex-start;
  }
  .benefits__svg {
    margin-right: 0;
    margin-bottom: 12px;
  }
  .benefits__content {
    padding-left: 240px;
    background-position: center left;
    background-size: 211px 187px;
  }
  .course__info {
    margin-bottom: 30px;
  }
  .course__btn {
    padding: 20px 55px;
  }
  .course__time {
    right: 10px;
  }
}
@media (max-width: 767.98px) {
  .container {
    padding: 0 33px;
  }
  .header {
    padding-bottom: 28px;
  }
  .header__wrapper {
    flex-direction: column;
    align-items: center;
  }
  .header__logo {
    margin-bottom: 24px;
  }
  .nav__item:not(:last-child) {
    margin-right: 56px;
  }
  .hero {
    background-position: center top;
  }
  .hero__content {
    padding: 77px 0 134px 0;
  }
  .hero__col {
    display: none;
  }
  .benefits {
    padding: 80px 0 56px 0;
  }
  .benefits__content {
    padding-left: 180px;
    background-position: top left;
    background-size: 150px 134px;
  }
  .instructors {
    padding: 75px 0 72px 0;
  }
  .courses {
    padding: 75px 0 79px 0;
  }
  .courses__card {
    align-items: center;
  }
  .course__title {
    margin-bottom: 9px;
    line-height: 23.4px;
  }
  .course__content {
    padding: 0 19px 30px 30px;
  }
  .course__info {
    margin-bottom: 35px;
  }
  .course__btn {
    padding: 20px 50px 20px 47px;
  }
  .course__time {
    top: 12px;
    right: 10px;
  }
}
@media (max-width: 575.98px) {
  .container {
    padding: 0 15px;
  }
  .header__nav {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    background-color: #2F2C45;
    padding: 24px 15px 105px 15px;
    z-index: 1000;
    overflow-y: auto;
    visibility: hidden;
    transform: translateY(-120%);
    transition: visibility 0.3s ease-in-out, transform 0.3s ease-in-out;
  }
  .header__nav.header__nav--active {
    visibility: visible;
    transform: none;
  }
  .header__wrapper {
    flex-direction: row;
  }
  .header__logo {
    margin-bottom: 0;
  }
  .header__svg {
    width: 72px;
    height: 16px;
  }
  .nav__close-btn {
    margin-bottom: 22px;
    display: block;
    background-color: #2F2C45;
    align-self: flex-end;
  }
  .nav__list {
    flex-direction: column;
  }
  .nav__item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 53px;
  }
  .burger {
    display: block;
    position: relative;
    width: 21px;
    height: 17px;
    border: none;
    background-color: transparent;
  }
  .burger__line {
    position: absolute;
    left: 25%;
    width: 100%;
    height: 2px;
    background-color: white;
    transform: translateX(-50%);
    border-radius: 10px;
  }
  .burger__line:first-child {
    top: 0;
  }
  .burger__line:nth-child(2) {
    top: 50%;
    transform: translateY(-50%) translateX(-50%);
  }
  .burger__line:last-child {
    bottom: 0;
  }
  .hero__content {
    padding: 50px 0 50px 0;
  }
  .hero__title {
    margin-bottom: 4px;
    font-size: 30px;
    line-height: 30px;
  }
  .hero__desc {
    margin-bottom: 45px;
    font-size: 16px;
    line-height: 21px;
  }
  .hero__btn {
    padding: 20.5px 53.5px;
    font-size: 16px;
    line-height: 19px;
  }
  .benefits {
    padding: 50px 0 48px 0;
  }
  .benefits__item:not(:last-child) {
    margin-bottom: 30px;
  }
  .benefits__content {
    padding-left: 0;
    margin-bottom: 25px;
    background-image: none;
  }
  .benefits__title {
    margin-bottom: 1px;
    font-size: 20px;
    line-height: 26px;
  }
  .benefits__card {
    flex-direction: row;
    align-items: center;
  }
  .benefits__svg {
    margin-right: 30px;
    margin-bottom: 0;
  }
  .instructors {
    padding: 47px 0 47px 0;
  }
  .instructors__item:nth-child(3) {
    margin-bottom: 24px;
  }
  .instructors__title {
    margin-bottom: 28px;
    font-size: 20px;
    line-height: 20px;
  }
  .instructors__name {
    font-size: 18px;
    line-height: 27px;
  }
  .instructors__img {
    border-top-right-radius: 41px;
    border-top-left-radius: 41px;
  }
  .courses {
    padding: 45px 0 51px 0;
  }
  .courses__title {
    margin-bottom: 26px;
    font-size: 20px;
    line-height: 20px;
  }
  .courses__card {
    min-height: 653px;
    align-items: flex-start;
  }
  .course-first {
    margin-bottom: 31px;
  }
  .course-third {
    margin-bottom: 30px;
  }
  .course__content {
    padding: 0 25px 30px 30px;
  }
  .course__info {
    margin-bottom: 83px;
  }
  .course__btn {
    padding: 20px 56px 20px 56px;
  }
}